import React from 'react';
import "../style.css"
import {PointTrackers,  MainPointTracker } from "../reused-components/pointTrackers.js";
import { AnswerIcon, MasterIcon } from '../reused-components/icons';
import { SideNav, Navbar } from "../reused-components/navigation.js";
import { buyReward, getAvailablePerksAndRewardsList, getUserPerksAndRewardsList, userCash } from '../backend';
import { isMobile } from 'react-device-detect';


export class PerksAndRewardsPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {isExtended: "extended"}
  }
  changeState = () => {
    if (this.state.isExtended === "extended") {
      this.setState({isExtended: "not-extended"})
    }
    else {
      this.setState({isExtended: "extended"})
    }
  }
  render() {
    return (
      <>
        <div style={{ position: "absolute" }}>
          <Navbar pageName="Perks and Rewards" sideNavAnimation={this.changeState} />
        </div>
        <div style={{ position: "absolute", zIndex: -1 }} className={this.state.isExtended}>
          <div style={{ display: "inline-block", position: "absolute" }}>
            <SideNav isExtended={"extended"} />
            </div>
          <div className="todo-outer-div" style={(isMobile ? {position: "fixed", width: "100vw", left: "-80vw", zIndex: -50} :(this.state.isExtended === "extended") ? {} : {width: "100vw"})}>
          <div style={{marginLeft: "40px"}}>
            <PointTrackers />
          </div>
          <div style={{width: "calc(100% - 40px)", height: "calc(100% - 10vh)", marginTop: "5vh", marginLeft: "40px"}}>
            <PerksAndRewardsMainContainer />
          </div>
          </div>
          <div>
          </div>
        </div>
      </>
    )
  }
}




class PerksAndRewardsMainContainer extends React.Component {
  constructor(props) {
    super(props)
    this.state = {rewardsList: [], boughtRewards: []}
    this.refreshRewardsList()
  }
  refreshRewardsList = async () => {
    var boughtRewards = await getUserPerksAndRewardsList()
    var rewardsList = await getAvailablePerksAndRewardsList()
    this.setState({rewardsList: rewardsList, boughtRewards: boughtRewards})
  }
  render() {
    return <div style={{height: "100%"}} key={JSON.stringify(this.state.rewardsList)}>{[
      this.state.rewardsList,
      this.state.rewardsList,
      this.state.rewardsList,
      this.state.rewardsList
    ].map((item, index) => (
      <div style={{width: "100%", height: "25%"}}>
        <PerksAndRewardsSubContainer content={item} index={index} boughtRewards={this.state.boughtRewards}/>
        </div>)
        )}</div>
  }
}

class PerksAndRewardsSubContainer extends React.Component {
  constructor(props) {
    super(props)
    this.index = this.props.index
    this.state = {content: this.props.content, buyPopUpContent: "", buyPopUpShow: false, index: null, boughtBool: false}
  }
  changeBoughtState = (index) => {
    var newContent = this.state.content;
    newContent[index][0] = !newContent[index][0]
    this.changePopUp()
    this.setState({content: newContent})
  }
  changePopUp = (index = null, boughtBool) =>{
    this.setState({buyPopUpShow: !this.state.buyPopUpShow, index: index, buyPopUpContent: this.state.content[index], boughtBool: boughtBool})
  }
  render() {
    
    return (
      <>
      
      <div style={{borderBottom: "2px solid lightgray", height: "100%", maxHeight: "100%"}} className="perks-rewards-scrollable">
        <div style={{height: "calc(22.5vh - 27px)"}}>
        {this.state.content.map((item,index) => {
        if (item.row_id !== this.props.index) {
          return null;
        }
        //var boughtBool = this.props.boughtRewards.includes(JSON.stringify(item.id))
        //alert(JSON.stringify(item))
        return (<div style={{display: "inline-block", marginLeft: "40px", height: "100%", textAlign: "center", cursor: "pointer"}} onClick={() => this.changePopUp(index, false)}>
          {//<div style={{marginBottom: "-15px", marginTop: "5%"}}>{item.reward_name}</div>
          }
          <svg xmlns="http://www.w3.org/2000/svg" xmlnsXlink="http://www.w3.org/1999/xlink" width="auto" height="75%" viewBox="0 0 177 191" style={{marginTop: "5%"}}>
  <defs>
    <pattern id={"pattern"+JSON.stringify(item.id)} preserveAspectRatio="none" width="100%" height="100%" viewBox="0 0 400 400">
      <image width="400" height="400" xlinkHref={item.image} />
    </pattern>
  </defs>
  {//<text id="Fundraising_lesson" data-name="Fundraising lesson" transform="translate(0 226)" font-size="21" font-family="SegoeUI-Semibold, Segoe UI" font-weight="600"><tspan x="0" y="0">{item.reward_name}</tspan></text>
  }
  <g id="Group_154" data-name="Group 154" transform="translate(3185 2307.366)">
    <rect id="Rectangle_1816" data-name="Rectangle 1816" width="124" height="50" rx="25" transform="translate(-3159 -2166.366)" fill="#f8f8f8"/>
    <rect id="Rectangle_1817" data-name="Rectangle 1817" width="58" height="25" rx="11" transform="translate(-3109 -2154.366)" fill="#fff"/>
    <text id="_1817" data-name="1817" transform="translate(-3092 -2136.366)" stroke="#707070" stroke-width="1" font-size="13" ><tspan x="0" y="0">{item.answer_points}</tspan></text>
    <rect id="Rectangle_1818" data-name="Rectangle 1818" width="124" height="50" rx="13" transform="translate(-3159 -2166.366)" fill="#f8f8f8"/>
    <path id="Polygon_15" data-name="Polygon 15" d="M24,0l8,16L24,32H8L0,16,8,0Z" transform="translate(-3116 -2157.366) rotate(90)" fill="#8b4389"/>
    <rect id="Rectangle_1819" data-name="Rectangle 1819" width="58" height="25" rx="11" transform="translate(-3109 -2154.366)" fill="#fff"/>
    <text id="C" transform="translate(-3141 -2134.366)" fill="#fff" font-size="24" font-weight="700" font-style="italic"><tspan x="0" y="0">C</tspan></text>
    <text id="_1999" data-name="1999" transform="translate(-3097 -2135.366)" font-size="17"  font-weight="600"><tspan x="0" y="0">{item.answer_points}</tspan></text>
  </g>
  <rect id="Image_11" data-name="Image 11" width="124" height="124" rx="26" transform="translate(26)" fill={`url(#${"pattern"+JSON.stringify(item.id)})`}/>
</svg>
<div style={{fontSize: "10px"}}>{item.reward_name}</div>
</div>

        )
       })}
       </div>
          </div>
          <BuyPopUp show={this.state.buyPopUpShow} content={this.state.buyPopUpContent} changePopUp={this.changePopUp} index={this.state.index}
      changeBoughtState={this.changeBoughtState} boughtBool={this.state.boughtBool}
      />
          </>
    
    )
  }
}

class BuyPopUp extends React.Component {
  render() {
    if (!this.props.show) {
      return <></>;
    }
    
    return (<div className="popup-background-div">
      <div className="popup-clickable-background-div"
      onClick={this.props.changePopUp}>
      </div>
      <div style={{ backgroundColor: "white",  zIndex: Math.pow(99,1000), padding: "20px", borderRadius: "20px", minHeight: "500px", minWidth: "400px",
    position: "relative", maxWidth: "90vw"
    }}><div style={{height: "400px", overflowY: "auto"}}>
        <div><img src={this.props.content.image} style={{height: "150px", width: "150px", borderRadius: 10000}} alt="reward"/>
        <div style={{display: "inline-block"}}>Price: {this.props.content.answer_points} CambioCoins</div>
        <div style={{fontWeight:"bold", marginTop: "25px"}}>{this.props.content.description_title}</div>
        <div style={{lineHeight: 1.5}}>{(this.props.content.description).split("\r\n").map(item => <div>{item}</div>)}</div>
        </div></div>
        <div style={{position: "absolute", bottom: "30px", left: "100px"}}>
        <div style={{marginBottom: "20px"}}>
          {(this.props.boughtBool ? `Do you want to spend?` :` Do you want to confirm your purchase?`)}
        </div>
        <button onClick={() => {
          buyReward(this.props.content.id)
          this.props.changeBoughtState(this.props.index)}} className="add-task-button">
          <p1 className="add-button-text">Confirm</p1>
        </button>
        <button onClick={this.props.changePopUp} className="cancel-button"><p1 className="add-button-text">Cancel</p1></button>
        </div>
      </div>
    </div>)
  }
}




class StarIcon extends React.Component {
  render() {
    return (
    <svg xmlns="http://www.w3.org/2000/svg" height="58" viewBox="0 0 24 24" width="auto" fill={(this.props.bought ?"#000000": "#717171"  )}><path d="M0 0h24v24H0z" fill="none"/><path d="M0 0h24v24H0z" fill="none"/><path d="M12 17.27L18.18 21l-1.64-7.03L22 9.24l-7.19-.61L12 2 9.19 8.63 2 9.24l5.46 4.73L5.82 21z"/></svg>
  )
  }
}

class RewardIcon extends React.Component {
  render() {
    return (<img src={this.props.imgUrl} style={{height: "58px", width: "58px", borderRadius: 10000, opacity: (this.props.bought? 1: 0.5)}} alt="reward with opacity"/>)
  }
}