import { openPopup, closePopup } from "./Popup";
import { VideoQuestionPopup } from "../VideoQuestionPopup";

export function activateGlobalPromptPopups() {const socket = new WebSocket('wss://cambio-socket.herokuapp.com/');
socket.addEventListener('message',  (event) => {
    if (event.data === "end_class") {
        window.location.href = "/#/"
    }
    var promptData = JSON.parse(event.data)
    if (promptData) {
    try {openPopup(<VideoQuestionPopup changePopUp={
        closePopup
    }
    videoQuestionData={promptData}
    />)}
    catch {
        closePopup()
    }
} 
    else {
        closePopup()
    }
  });
}